import React, { Component } from 'react';
import './App.css';
import Button from 'react-bootstrap/lib/Button';
import Col from 'react-bootstrap/lib/Col';
import ButtonToolbar from 'react-bootstrap/lib/ButtonToolbar';

const ShowVoucher = ({ ticket, history }) => (

	<Col xs={12} className="response-for-check">
        
          <div className="response-data">
            <h2>   </h2>
			<table>
				<tr>
					<th id="serialNumber__text"> :</th>
					<td>{ticket.serialNumber}</td>
				</tr>
				<tr>
					<th> :</th>
					<td>{ticket.fn}</td>
				</tr>
				<tr>
					<th> :</th>
					<td>{ticket.fd}</td>
				</tr>
				<tr>
					<th>  :</th>
					<td>{ticket.fpd}</td>
				</tr>
				<tr>
					<th>:</th>
					<td>{ticket.guaranteeTime}</td>
				</tr>
			</table>
          </div>
          <br/>
        	<Button onClick={history.goBack} className="custom-btn"></Button>

      </Col>
	);	

export default ShowVoucher;


